@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


a{
  color:black !important;
}

.brand-logo{
  font-family: 'Montserrat', sans-serif;
}

.item{
  width: 30%;
}

.home{
  
}

.class-content,h5{
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.card-image{
  padding-left: 20px;
  padding-right: 20px;
}

.home-card{
  max-width: 500px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  margin: 26px auto;
}

.input-field{
  max-width: 700px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  margin: 26px auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  text-align: center;
}

.waves-effect{
  
}

.file-path validate{
  color:blue;
}

.mycard{
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  max-width: 500px;
  margin: 10px auto;
}

.auth-card{
  padding: 20px;
  text-align: center;
  max-width: 400px;
  margin: 10px auto;
}

.input-field input[type=text]:focus {
  border-bottom: 1px solid rgb(8, 93, 252) !important;
  box-shadow: 0 1px 0 0 rgb(8, 93, 252)  !important;
}
.input-field input[type=password]:focus {
  border-bottom: 1px solid rgb(8, 93, 252) !important;
  box-shadow: 0 1px 0 0 rgb(8, 93, 252)  !important;
}

.card{
  padding-top:0.25px;
}
#toast-container{
  top:30px !important;
  right: 20px !important;
  left: auto !important;
}

.gallery{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.item{
  width: 150px;
  height: 150px;
  margin: 10px;
}

.collection-item{
  width: 100%;
}

.material-icons:hover{
  cursor: pointer;
}


